import { apiGet, apiPost } from "./api";
// 获取验证码
export function getyzm() {
    return new Promise((resolve) => {
        apiGet('index/getcode').then(res => {
            resolve(res)
      })
    })
}
// 登录
export function denlu(params) {
     return new Promise((resolve) => {
         apiPost('index/login',params).then(res => {
            resolve(res)
      })
    })
}
// 注册
export function zhuce(params) {
     return new Promise((resolve) => {
         apiPost('index/register',params).then(res => {
            resolve(res)
      })
    })
}
// 获取列表
export function getuser(params) {
    return new Promise((resolve) => {
        apiPost('call/getsuperes', params).then(res => {
            resolve(res)
        })
    })
}
// 获取单个
export function getuseros(params) {
    return new Promise((resolve) => {
        apiPost(`call/getsuperbyid?id=${params}`).then(res => {
            resolve(res)
        })
    })
}
// 修改状态
export function updasta(params,num) {
    return new Promise((resolve) => {
        apiPost(`call/upsuperstatus?id=${params}&status=${num}`).then(res => {
            resolve(res)
        })
    })
}
//添加
export function addli(params) {
    return new Promise((resolve) => {
        apiPost(`call/addsuper`,params).then(res => {
            resolve(res)
        })
    })
}
