<template>
  <div class="home">
    <img src="../assets/ewm.png" alt="" class="ewm">
    <div class="mian">
      <img src="../assets/logo.png" alt="" class="logo">
      <div class="dlx">
        <div class="dlx-img">
          <img src="../assets/pic.png" alt="">
        </div>
        <div class="denglu">
          <img src="../assets/text.png" alt="" style="margin-left: 60px;">
          <div>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm" status-icon
              hide-required-asterisk style="width: 450px;">
              <el-form-item prop="name" style="margin-bottom: 35px;">
                <el-input v-model="ruleForm.name" placeholder="用户名" prefix-icon="el-icon-user"></el-input>
              </el-form-item>
              <el-form-item prop="pass" style="margin-bottom: 35px;">
                <el-input placeholder="密码" v-model="ruleForm.pass" show-password prefix-icon="el-icon-lock"></el-input>
              </el-form-item>
              <el-form-item prop="yanz" style="text-align: left;margin-bottom: 35px;">
                <el-input v-model="ruleForm.yanz" style="width: 50%;margin-right: 2%;" prefix-icon="el-icon-chat-square"
                  placeholder="验证码"></el-input>
                <el-button id="ma" style="margin-right: 2%;width: 130px;" @click="yam()">获取验证码</el-button>
                <span class="yan" style="display: inline-block;">{{ ma }}</span>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" style="width: 450px;" @click="submitForm('ruleForm')">立即登录</el-button>
              </el-form-item>
            </el-form>
          </div>
          <a href="" @click.prevent="zhuc">注册账户</a>
        </div>
      </div>
    </div>
    <p style="position: absolute;bottom: 1%; left: 50%;transform: translate(-50%, 50%);width: 100%;font-size: 14px;">
