    </p>
  </div>
</template>

<script>
import { getyzm, denlu } from "../api/https";
export default {
  data() {
    return {
      ma: '',
      n: '',
      mia: 60,
      ruleForm: {
        name: '',
        pass: '',
        yanz: '',
      },
      rules: {
        name: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
        ],
        pass: [
          { required: true, message: '请输入密码', trigger: 'blur' },
        ],
        yanz: [
          { required: true, message: '请输入验证码', trigger: 'blur' },
          { min: 4, max: 4, message: '请输入4位验证码', trigger: 'blur' },
          {
            validator: (rule, value, callback) => {
              if (value == this.ma) {
                callback()
              } else {
                callback(new Error('验证码错误'))
              }
            }, trigger: 'blur'
          }
        ],
      }
    };
  },
  created() {
    this.yam()
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          denlu({
            vercode: this.ruleForm.yanz,
            name: this.ruleForm.name,
            pwd: this.ruleForm.pass,
          })
            .then((response) => {
              
              if (response.code == 0) {
                localStorage.setItem('user', response.data[0].user)
                this.$router.replace('/about')
              } else {
                this.$message({
                  message: '登录失败',
                  type: 'error'
                });
              }
            })
            .catch((error) => {
              console.error(error);
            })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    zhuc() {
      this.$router.replace('/zhuc')
    },
    yam() {
      this.$axios
      getyzm()
        .then((response) => {
          console.log();
          this.jqueryData = response;
          this.ma = response.msg;
          console.log(response.msg);
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
};
</script>

